				"html": "<p>I separate JavaScript Projects into 4 categories. Itâ€™s important for me to remember these categories when learning about projects so that I can optimize my time spent learning, building and tooling with JavaScript. Currently (2017), JavaScript churn is real and it is easy to be overwhelmed. I find that when I break down JavaScript projects into these categories, Iâ€™m able to invest my time appropriately and pick tools faster. </p>\n<h2>The Core 4</h2>\n<ul>\n<li>Plugins ğŸ”Œ</li>\n<li>Libraries ğŸ“š</li>\n<li>Frameworks ğŸ–¼</li>\n<li>Services ğŸ”§</li>\n</ul>\n<h2>Core 4 Definitions</h2>\n<p>ğŸ”ŒÂ Â <strong>Plugins:</strong> are a small usable functions. They should mainly do 1 thing and be usable across a wide spectrum of Frameworks, Libraries, and Services. If I canâ€™t make a plugin work within several minutes, then I move on. </p>\n<p>ğŸ“šÂ Â <strong>Libraries:</strong> are groups of utilities. Examples of projects I consider libraries are <a href=\"http://jquery.com/\">jQuery</a> and <a href=\"https://lodash.com/\">LoDash</a>. Libraries create ways for their users to more easily do things like manipulate html. I find I change Libraries minimally so I tend to go for rock solid products that are stable and well documented.</p>\n<p>ğŸ–¼Â Â <strong>Frameworks:</strong> are projects like <a href=\"https://angular.io/docs/js/latest/\">Angular</a>, <a href=\"http://emberjs.com/\">Ember</a>, and <a href=\"https://facebook.github.io/react/\">React</a>. They establish patterns for writing entire systems of code. Frameworks are the cornerstone of products I build so when choosing a framework I build little apps and think about how that Frameworkâ€™s functionality will apply to the project I will be using it for. I invest a few days into understanding the basics of a Framework to make sure itâ€™s the right choice for a product.</p>\n<p>ğŸ”§Â Â <strong>Services:</strong> are things like compilers, task runners, or bundlers (<a href=\"http://webpack.github.io/\">Webpack</a> or <a href=\"http://babeljs.io/\">Babel</a>). Services make it easier to build Frameworks, Libraries and Plugins. Services support the other 3 categories of JavaScript projects so time spent working on them is during configuration. I usually spend a few hours configuring Services for projects. </p>\n<h2>Summary</h2>\n<p>Separating JavaScript Projects into 4 categories gives my brain a digestible way to approach JavaScript projects. Itâ€™s very easy to hop on twitter and read about new awesome things and feel overwhelmed. When binging on new things, itâ€™s nice to bucket them into categories to manage time spent learning versus time spent building. ğŸš€</p>\n<p><strong>Update:</strong> Every single person that has read this post thinks that I am wrong and has valid strong opinions on why. Wanna [tweet](//twitter.com/share?url=<a href=\"https://jeffry.in%7B%7B\">https://jeffry.in{{</a> page.url }}&#x26;text={{ page.title }}&#x26;via={{ site.twitter_username }}) about it? Please do! ğŸ™</p>",
